import { convertFromRaw, convertToRaw, Editor, EditorState, RichUtils } from 'draft-js';
import { useEffect, useState } from 'react';
import { Button, Spinner } from 'react-bootstrap';
import './TextEditor.css'

const TextEditor = ({data, isReadOnly, onUpdate}) => {
    const [editorState, setEditorState] = useState(EditorState.createEmpty());
    useEffect(() => {
        if (data) {
            const newState = EditorState.createWithContent(convertFromRaw(JSON.parse(data)));
            setEditorState(newState);
        }
    },[]);
console.log(data);
    function handleKeyCommand(command){
        const newState = RichUtils.handleKeyCommand(editorState, command);

        if (newState) {
            setEditorState(newState);
            return true;
        } else {
            return false;            
        }
    }
    
    function toggleBlockType(blockType) {
        setEditorState(RichUtils.toggleBlockType(editorState, blockType));        
    }
    
    function toggleInlineStyle(inlineStyle) {
        setEditorState(RichUtils.toggleInlineStyle(editorState, inlineStyle));        
    }

    function onTab(e) {
        const maxDepth = 3;
        setEditorState(RichUtils.onTab(e, editorState, maxDepth));
    }

    function handleOnChange(editorState) {
        const contentState = editorState.getCurrentContent();
        const newState = JSON.stringify(convertToRaw(contentState));
        // setEditorState(editorState);
        onUpdate(newState);
    }

    // if (!editorState) {
    //     return <div className='images-spinner row justify-content-center mt-3'>
    //                 <Spinner animation="border" variant="warning" />
    //             </div>
    // }

    return (
        <div className='c-text-editor'>
            <Button variant='outline-warning' onClick={() => toggleInlineStyle('BOLD')}>B</Button>
            <Editor editorState={editorState} onChange={handleOnChange} readOnly={isReadOnly} 
                handleKeyCommand={handleKeyCommand} onTab={onTab}/>
        </div>
    );
}

export default TextEditor;